  setResfn(res) {
      if (res && res.status === 1) {
        return true;
      } else if (res && res.status === 2) {
          this.nativeService.presentToast(res.msg || res.message);
          return true;
      } else if (res && res.status === 3) {
        this.nativeService.presentToast('!');
        this.logints();
        return false;
      } else {
        const msg = res ? (res.msg || res.message) : '';
        this.requestFailed('', '',  msg);
        return false;
      }
      // return true;
  }
// 
  paramFormat(data: any): string {
    const obj: any = new FormData();
    Object.keys(data).map(key => {
      if (!data[key] && data[key] != 0) {
        return false;
      }
      obj.append(key, data[key]);
    });
    return obj;
  }
  private logints() {
    this.nav.navigateRoot('/login');

  }
  private requestFailed(url: string, err, rmsg ?: string) {
    this.nativeService.hideLoading();
    let msg = url ? url : '';
    if (!err) {
      msg += rmsg;
    } else {
      // alert(err)
      // alert(JSON.stringify(err))
      const status = err.status;
      if (!this.nativeService.isConnecting()) {
      // if (false) {
        msg += '';
      } else {
        if (status === 0) {
          msg += '';
        } else if (status === 404) {
          msg += '';
        } else if (status === 500) {
          msg += '';
        } else {
          msg += '';
        }
      }
    }

    this.nativeService.presentToast(msg);
    // this.alertCtrl.create({
    //   title: msg,
    //   subTitle: status,
    //   buttons: [{text: ''}]
    // }).present();
  }
  clearUser() {
    this.user = null;
  }
